
import { computed, defineComponent } from "vue";
export default defineComponent({
  props: {
    date: {
      type: Date,
      required: true,
    },
  },
  setup(props) {
    return {
      dateString: computed(() => {
        let day = props.date.getDate();
        let month = props.date.getMonth() + 1;
        let year = props.date.getFullYear();
        return `${day}.${month}.${year}`;
      }),
    };
  },
});
